<template>
	<b-modal title="Search Address" size="lg" v-model="showModal" @shown="handleOpen">
		<div class="geocodeMap" id="viewDiv">
		</div>
		<b-btn slot="modal-footer" block @click="saveResult">Save</b-btn>
	</b-modal>
</template>

<script>
import {get,set,copy} from 'vuex-pathify'
import * as esriLoader from 'esri-loader'

	export default {
		data: function() {
			return {
				geometry: {
					location: "",
					x: "",
					y: ""
				}
			}
		},
		computed: {
			showModal: {
				get() {
					return this.$store.get('geocodeModal')
				},
				set(value) {
					this.$store.set('geocodeModal', value)
				}
			},
		},
		mounted: function() {
			esriLoader.loadCss('https://js.arcgis.com/4.9/esri/css/main.css');
			esriLoader.loadModules(['esri/Map','esri/views/MapView','esri/widgets/Search'])
			.then(([Map, MapView,Search])=> {
				const map = new Map({
					basemap: 'streets-navigation-vector'
				});

				var view = new MapView({
					map: map,
					container: 'viewDiv',
					center: [-79.3854,43.64869],
            		zoom: 10
				});

				view.on("mouse-wheel", function(event){
				  // prevents zooming with the mouse-wheel event
				  event.stopPropagation();
				});

				this.mysearch = new Search({
					view:view
				});

				view.ui.add(this.mysearch, 'top-right');

				this.mysearch.on('select-result',(event)=> {
					console.log(event);
					this.geometry.location = event.result.name;
					this.geometry.x = event.result.feature.geometry.longitude;
					this.geometry.y = event.result.feature.geometry.latitude;
				});
			});
		},
		methods: {
			saveResult: function() {
				this.showModal = false;
				let dir = this.$store.get('geocodeReq');
				this.$store.set(dir, JSON.parse(JSON.stringify(this.geometry)))
			},
			handleOpen: function() {
				this.mysearch.clear();
				this.mysearch.focus();
			}
		}
	}
</script>

<style>
#viewDiv {
	height: 70vh;
}
</style>