<template>
	<b-container fluid class="py-3">
		<b-form>
			<h4>{{`${type} ${count}`}}</h4>
			<div class="d-flex flex-wrap mt-2">
				<template v-for="(elem,key) in inputs.attributes">
					<FormInput  :key="key" :dir="`${dir}.attributes.${key}`" v-if="inputs.attributes[key].display"/>
				</template>
				<Geocoder v-if="type == 'Order' || type == 'Depot'" :dir="`${dir}.geometry`"/>
			</div>
		</b-form>
	</b-container>
</template>

<script>
	import FormInput from '@/components/FormInput.vue'
	import Geocoder from '@/components/Geocoder.vue'

	export default {
		name: 'FormBlock',
		props: {
			type: String,
			count: Number,
			dir: String
		},
		components: {
			FormInput,
			Geocoder
		},
		computed: {
			inputs: function() {
				return this.$store.get(this.dir)
			}
		}
	}
</script>