<template>
	<div class="d-flex flex-wrap">
		<div class="form-group mr-3">
			<div class="row">
				<div class="col-md-12">
					<label>Location</label>
					<b-input type="text" v-model="location" disabled/>
				</div>
			</div>
		</div>
		<div class="form-group mr-3">
			<div class="row">
				<div class="col-md-12">
					<label>Lat</label>
					<b-input type="number" v-model="y"/>
				</div>
			</div>
		</div>
		<div class="form-group mr-3">
			<div class="row">
				<div class="col-md-12">
					<label>Long</label>
					<b-input type="number" v-model="x"/>
				</div>
			</div>
		</div>
		<div  class="custom">
			<b-button @click="setGeocodeReq">
				Search Address
			</b-button>
			
		</div>
	</div>
</template>

<script>

	import {get} from 'vuex-pathify'

	export default {
		name: 'Geocoder',
		props: {
			dir: String
		},
		computed: {
			x: {
				get() {
					return this.$store.get(this.dir+'.x')
				},
				set(value) {
					this.$store.set(this.dir+'.x', value)
				}
			},
			y: {
				get() {
					return this.$store.get(this.dir+'.y')
				},
				set(value) {
					this.$store.set(this.dir+'.y', value)
				}	
			},
			location: {
				get() {
					return this.$store.get(this.dir+'.location')
				},
				set(value) {
					this.$store.set(this.dir+'.location', value)
				}
				
			}
		},
		methods: {
			setGeocodeReq: function() {
				this.$store.set('geocodeReq', this.dir);
				this.$store.set('geocodeModal', true);
			}
		}
	}
</script>

<style> 
.custom {
	margin-top: 32px !important;
}
</style>