<template>
	<transition name="fade">
		<div class="about">

			<b-nav tabs>
			<b-nav-item v-for="item in navItems" :key="item.title" @click="activeItem = item.title" :active="item.title == activeItem">{{item.title}}</b-nav-item>
			</b-nav>
			<div v-show="activeItem=='Orders'" id="orderForm" class="bg-light pb-3">
				<FormBlock v-for="(index) in orderLen" :key="index" :count="index" type="Order" :dir="`requiredParam@Order.${index}`"/>
				<b-button class="mr-3" @click="add('Order')">Add Order</b-button>
				<b-button @click="remove('Order')" :disabled="(orderLen<2)?true:false">Remove Order</b-button>
			</div>
			<div v-show="activeItem=='Depots'" id="depotForm" class="bg-light pb-3">
				<FormBlock v-for="(index) in depotLen" :key="index" :count="index" type="Depot" :dir="`requiredParam@Depot.${index}`" />
				<b-button class="mr-3" @click="add('Depot')">Add Depot</b-button>
				<b-button @click="remove('Depot')" :disabled="(depotLen<2)?true:false">Remove Depot</b-button>
			</div>
			<div v-show="activeItem=='Routes'" id="routeForm" class="bg-light pb-3">
				<FormBlock v-for="(index) in routeLen" :key="index" :count="index" type="Route" :dir="`requiredParam@Route.${index}`"/>
				<b-button class="mr-3" @click="add('Route')">Add Route</b-button>
				<b-button @click="remove('Route')" :disabled="(routeLen<2)?true:false">Remove Route</b-button>
			</div>
			<b-container v-show="activeItem=='Optional Parameters'" fluid class="pt-4 bg-light">
				<b-form>
					<div class="d-flex flex-wrap">
						<template v-for="(elem, key) in formOptData">
							<FormInput  v-if="elem.display" :key="key" :dir="`optionalParam@${key}`"></FormInput>
						</template>
					</div>
				</b-form>
			</b-container>
			<b-container v-show="activeItem=='Settings'" class="mt-3">
				<b-button @click="paramModal = !paramModal" class="mr-2">Choose Parameters</b-button>
				<b-button @click="setDefault" class="mr-2">Save default</b-button>
				<b-button @click="clearDefault">Clear Default</b-button>
			</b-container>
			<b-container fluid class="d-flex">
				<b-button v-show="activeItem!='Settings'" @click="submit" variant="primary" class="my-4 mr-auto">Submit</b-button>
			</b-container>
			<GeocodeModal/>
			<b-modal v-model="paramModal">
				<div v-for="(data, type) in formData" :key="type">
					<h5>{{type}}</h5>
					<div>
						<b-form-checkbox v-for="(elem, index) in data['1'].attributes" 
						:key ="index"
						v-model="elem.display" @input="(event)=>cascade(event,type,index)" :disabled="elem.isRequired" v-b-tooltip.hover :title="elem.tooltip">{{elem.labelText}}</b-form-checkbox>
					</div>
				</div>
			</b-modal>
		</div>
	</transition>
</template>

<script>
	import FormBlock from '@/components/FormBlock.vue'
	import FormInput from '@/components/FormInput.vue'
	import GeocodeModal from '@/components/GeocodeModal.vue'
	import * as esriLoader from 'esri-loader'
	import {mapGetters} from 'vuex'
	import {get} from 'vuex-pathify'

	export default {
		name: 'Form',
		components: {
			FormBlock,
			GeocodeModal,
			FormInput
		},
		data: function() {
			return {
				navItems: [
					{title: 'Orders'},
					{title: 'Depots'},
					{title: 'Routes'},
					{title: 'Optional Parameters'},
					{title: 'Settings'},
				],
				activeItem: 'Orders',
				paramModal: false
			}
		},
		computed: {
			...mapGetters([
				'orderLen',
				'depotLen',
				'routeLen'
			]),
			formData: get('requiredParam'),
			formOptData: get('optionalParam'),

		},
		methods: {
			cascade: function(val,type,index) {
				for (var key in this.formData[type]) {
					this.$store.set(`requiredParam@${type}.${key}.attributes.${index}.display`, val) 
				}
			},
			add: function(type) {
				const toAdd = JSON.parse(JSON.stringify(this.formData[type]['1']));
				for (var key in toAdd.attributes) {
					if (toAdd.attributes[key].inputType !== 'select') {toAdd.attributes[key].value = ""}
				}
				for (var key2 in toAdd.geometry) {
					toAdd.geometry[key2] = ""
				}
				const val = (type == 'Order') ? this.orderLen : (type == 'Depot') ? this.depotLen : (type == 'Route') ? this.routeLen : null
				this.$store.set(`requiredParam@${type}.${val+1}`, toAdd);
			},
			remove: function(type) {
				const val = (type == 'Order') ? this.orderLen : (type == 'Depot') ? this.depotLen : (type == 'Route') ? this.routeLen : null
				const toAdd = JSON.parse(JSON.stringify(this.formData[type]));
				delete toAdd[val]
				this.$store.set(`requiredParam@${type}`, toAdd)
			},
			submit: function() {
				this.$store.set('jobId', null);
				this.$store.set('jobStatus', null);
				this.$store.set('messages', []);
				var copy = JSON.parse(JSON.stringify(this.formData))
				for (var type in copy) {
					copy[type] = Object.values(copy[type])
					copy[type].forEach((elem,i)=>{
						for (var field in elem.attributes) {
							if (elem.attributes[field].display) {
								copy[type][i].attributes[field] = (elem.attributes[field].inputType == 'datetime') ? new Date(elem.attributes[field].value)*1:elem.attributes[field].value 
							} else {
								delete copy[type][i].attributes[field]
							}
							
						}
					})
				}
				var optParam = {};
				for (var p in this.formOptData) {
					let tmp = this.formOptData[p];
					if (tmp.value) {
						optParam[p] = (tmp.inputType == 'datetime') ? new Date(tmp.value)*1:tmp.value
					}
				}
				esriLoader.loadModules(["esri/tasks/Geoprocessor"]).then(([Geoprocessor])=>{
					var task = new Geoprocessor(`https://logistics.arcgis.com/arcgis/rest/services/World/VehicleRoutingProblem/GPServer/SolveVehicleRoutingProblem`);
					var jobParam = {
						orders: JSON.stringify({features: copy.Order}),
						depots: JSON.stringify({features: copy.Depot}),
						routes: JSON.stringify({features: copy.Route}),
						save_route_data: true,
						...optParam
					};
					this.$store.set('processJob', task.submitJob(jobParam))
					this.$router.push('map');
				});
			},
			setDefault: function() {
				const defaultObj = JSON.stringify({
					defReq: this.$store.copy('requiredParam'), 
					defOpt: this.$store.copy('optionalParam')
				});
				localStorage.setItem('formDefault', defaultObj)

			},
			clearDefault: function() {
				localStorage.removeItem('formDefault');
				this.$store.set('clear');
			}
		}
	}
</script>

<style>
.custom-control-label {
-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */
	-khtml-user-select: none; /* Konqueror HTML */
		-moz-user-select: none; /* Firefox */
		-ms-user-select: none; /* Internet Explorer/Edge */
			user-select: none; /* Non-prefixed version, currently notsupported by Chrome and Opera */
}

.nav-link {
	color: #495057;
}
	
