<template>
	<div v-if="data.display" class="form-group mr-3">
		<div class="row">
			<div class="col-md-12">
				<label v-b-tooltip.hover :title="data.tooltip">
					{{data.labelText}}
				</label>
				<div class="row" v-if="data.inputType =='datetime'">
					<div class="col">
						<date-picker  lang="en" type="datetime" v-model="value" format="MM-DD-YYYY HH:mm" placeholder=""/>
					</div>
				</div>
				<b-form-input v-else-if="data.inputType == 'number'" v-model="value" type="number"/>
				<b-form-input v-else-if="data.inputType == 'text'" v-model="value" type="text" />
				<div class="row" v-else-if="data.inputType == 'select'">
					<div class="col">
						<b-form-select v-model="value" :options="data.domain" class="pr-4"/>
					</div>
				</div>
				<div class="row" v-else-if="data.inputType == 'selectFromDepotName'">
					<div class="col">
						<b-form-select v-model="value" :options="domain" class="pr-4"/>
					</div>
				</div>
				<div class="row" v-else-if="data.inputType == 'travelModes'">
					<div class="col">
						<b-form-select v-model="value" :options="travelModes" class="pr-4"/>
					</div>
				</div>
				<orderPair v-else-if="data.inputType=='orderPair'" v-model="value" />
			</div>
		</div>
	</div>
</template>

<script>

	import DatePicker from 'vue2-datepicker'
	import * as esriLoad from 'esri-loader'

	export default {
		name: 'FormInput',
		props: {
			dir: String
		},
		components: {
			DatePicker,
		},
		data: function() {
			return {
				travelModes: [],
			}
		},
		computed: {
			data: function() {
				return this.$store.get(this.dir)
			},
			value: {
				get() {
					return this.$store.get(this.dir+'.value')
				},
				set(value){
					this.$store.set(this.dir+'.value', value)
				}
			},
			domain: function() {
				if (this.data.inputType == 'selectFromDepotName') {
					let copy = this.$store.copy('requiredParam@Depot');
					var options = [];
					for (var key in copy) {
						options.push(copy[key]['attributes']['Name']['value'])
					}
					return options;
				} else {
					return null
				}
			}
		},
		created: function() {
			if (this.data.inputType == 'travelModes') {
				esriLoad.loadModules(["esri/request"]).then(([esriRequest])=>{
					esriRequest('https://logistics.arcgis.com/arcgis/rest/services/World/Utilities/GPServer/GetTravelModes/execute', {
						method: 'post',
						query: {
							f: 'json'
						}
					}).then((response)=>{
						console.log(response);
						response.data.results[0].value.features.forEach((mode)=>{
							this.travelModes.push(mode.attributes.Name)
							if (mode.attributes.TravelModeId == response.data.results[1].value) this.value = mode.attributes.Name
						})
						
					})
				})
			}
		}
	}

</script>

<style> 
.mx-input {
	
    display: block;
    width: 100%;
    height: calc(2.25rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    -webkit-box-shadow: none;
}

.mx-datepicker {
	width: 221px !important;
}

.mx-calendar {
	font: 400 13.3333px Arial !important;
}


</style>